.answer-item {
  border: 1px solid #fff;
	border-radius: 5px;
	padding: 5px 10px;
	margin-bottom: 5px;
	cursor: pointer;
}

.answer-item:hover {
	opacity: .7;
	transition: opacity .3s ease-in-out;
}

.answer-item_success {
	background: rgba(161, 240, 69, .7);
}

.answer-item_error {
	background: rgba(240, 87, 108, .7);
}
